import React, { Component } from 'react';

class Resume extends Component {
  render() {

    if(this.props.data){
      var skillmessage = this.props.data.skillmessage;
      var education = this.props.data.education.map(function(education){
        return <div key={education.school}><h3>{education.school}</h3>
        <p className="info"style={{marginBottom:'10px'}}>{education.degree} <span>&bull;</span><em className="date">{education.graduated}</em></p>
        <p style={{marginBottom:'10px'}}>{education.description}</p>
        <p style={{marginBottom:'10px'}}>{education.description2 || ""}</p>
        </div>
      })
      var work = this.props.data.work.map(function(work){
        return <div key={work.company}><h3>{work.company}</h3>
            <p className="info">{work.title}<span>&bull;</span> <em className="date">{work.years}</em></p>
            <p style={{marginBottom:'10px'}}><b>Tech: </b>{work.tech}</p>
            <p style={{marginBottom:'10px'}}>{work.description}</p>
        </div>
      })
      var skills = this.props.data.skills.map(function(skills){
        var className = 'bar-expand '+skills.name.toLowerCase();
        return <li key={skills.name}><span style={{width:skills.level}}className={className}></span><em>{skills.name}</em></li>
      })
    }

    return (
      <section id="resume">

      <div className="row education">
         <div className="three columns header-col">
            <h1><span>Education</span></h1>
         </div>

         <div className="nine columns main-col">
            <div className="row item">
               <div className="twelve columns">
                 {education}
               </div>
            </div>
         </div>
      </div>


      <div className="row work">

         <div className="three columns header-col">
            <h1><span>Work</span></h1>
         </div>

         <div className="nine columns main-col">
          {work}
        </div>
    </div>



      <div className="row skill">

         <div className="three columns header-col">
            <h1><span>Skills</span></h1>
         </div>

         <div className="nine columns main-col">

            <p >{skillmessage}</p>
            <p style={{marginBottom:'10px'}}> <span style={{fontWeight:'bold', color:'black'}}>Programming Languages:</span> Java, Python, C</p>
            <p style={{marginBottom:'10px'}}> <span style={{fontWeight:'bold', color:'black'}}>Web Technologies:</span> JavaScript, React, Redux, Node, Express, Flask, HTML, CSS</p>
            <p style={{marginBottom:'10px'}}> <span style={{fontWeight:'bold', color:'black'}}>Database Technologies:</span> SQL, MySQL, NoSQL, MongoDB</p>
            <p style={{marginBottom:'10px'}}> <span style={{fontWeight:'bold', color:'black'}}>Miscellaneous:</span> AWS, Looker, OpenCV, Android Studio, Vistual Studio, Git, Linux/UNIX OS  </p>

				{/* <div className="bars">
				   <ul className="skills">
					  {skills}
					</ul>
				</div> */}
			</div>
      </div>
   </section>
    );
  }
}

export default Resume;
